const API_URL = 'https://emerald-reddit-backend.herokuapp.com';
import axios from '../utilities/setRequestHeaderToken';

const callApi = (endpoint, method, body) => {
  return axios({
    method: method,
    url: `${API_URL}${endpoint}`,
    headers: {
      'Content-Type': 'application/json',
      'Accept': 'application/json',
    },
    data: body,
  });
};

export default callApi;
