'use strict';

const hourInSeconds = 3600;
const dayInSeconds = 86400;
const monthInSeconds = 2592000;
const yearInSeconds = 31536000;

function relativeTimeFromTimestamp(timestamp) {
  if (isNaN(timestamp) || !Number.isInteger(timestamp)) {
    return undefined;
  }
  let currentTimestamp = Math.floor(Date.now() / 1000);
  let differenceInSeconds = currentTimestamp - timestamp;
  if (differenceInSeconds < hourInSeconds) {
    return `Just now`;
  } else if (differenceInSeconds < dayInSeconds) {
    let hourCounter = Math.floor((differenceInSeconds) / hourInSeconds);
    return `${hourCounter} hour${hourCounter > 1 ? 's' : ''} ago`;
  } else if (differenceInSeconds < monthInSeconds) {
    let dayCounter = Math.floor((differenceInSeconds) / dayInSeconds);
    return `${dayCounter} day${dayCounter > 1 ? 's' : ''} ago`;
  } else if (differenceInSeconds < yearInSeconds) {
    let monthCounter = Math.floor((differenceInSeconds) / monthInSeconds);
    return `${monthCounter} month${monthCounter > 1 ? 's' : ''} ago`;
  } else {
    let yearCounter = Math.floor((differenceInSeconds) / yearInSeconds);
    return `${yearCounter} year${yearCounter > 1 ? 's' : ''} ago`;
  }
}

export default relativeTimeFromTimestamp;
