'use strict';

import BootstrapDropdown from 'react-bootstrap/Dropdown';
import React, { Component } from 'react';
import styles from './styles.css';
import PropTypes from 'prop-types';

class Dropdown extends Component {
  constructor(props) {
    super(props);
  }

  createDropdownItems() {
    if (!this.props.dropdownList) {
      return null;
    }
    return this.props.dropdownList.map((option, index) => <BootstrapDropdown.Item href={`channels/${option.href}`} className={styles.dropdownItems} key={index}>
      {option.image &&
        <span>
          <img src={option.image} height="30" width="30" />
        </span>
      }
      <span>
        {option.text}
      </span>
    </BootstrapDropdown.Item >);
  }

  render() {
    return (
      <BootstrapDropdown >
        <BootstrapDropdown.Toggle variant={styles.dropdownActive} className={styles.dropdownButton}>
          {this.props.buttonImage &&
            <span>
              <img src={this.props.buttonImage} height="30" width="30" />
            </span>
          }
          <span>
            {this.props.defaultValue ? this.props.defaultValue : "-- Please select --"}
          </span>
        </BootstrapDropdown.Toggle>
        <BootstrapDropdown.Menu className={styles.dropdownList}>
          {this.createDropdownItems()}
        </BootstrapDropdown.Menu>
      </BootstrapDropdown>
    );
  }
}

Dropdown.propTypes = {
  dropdownList: PropTypes.arrayOf(PropTypes.shape({
    href: PropTypes.string,
    image: PropTypes.string,
    text: PropTypes.string,
  })),
  buttonImage: PropTypes.string,
  defaultValue: PropTypes.string,
};

export default Dropdown;
