import React, { Component } from 'react';
import Dropdown from '../Dropdown/index';
import styles from './style.css';
import logoFull from '../../img/logoFull.png';
import ButtonWithProps from '../ButtonWithProps';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import getUsernameFromToken from '../../utilities/getUsernameFromToken';
import callApi from '../../API/callApi';

class Header extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    this.props.loadChannels();
  }

  handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('refreshToken');
    this.props.logout();
  }

  render() {
    const { isLoggedIn, channels } = this.props;
    const dropdownListToBeRendered = channels.map(channel => {
      return {
        href: channel.id.toString(),
        text: channel.name,
        image: channel.background_image,
      };
    });

    let headerMessage, registerOrSubmitButton, logInOrOutButton;
    if (isLoggedIn) {
      const username = getUsernameFromToken();
      headerMessage = <p className={styles.userName}>Welcome, {username}!</p>;
      logInOrOutButton = <ButtonWithProps onClick={this.handleLogout} url='/' label='log out' />;
      registerOrSubmitButton = <ButtonWithProps url='/submit' label='submit'></ButtonWithProps>;
    } else {
      headerMessage = <p className={styles.userName}>Welcome!</p>;
      registerOrSubmitButton = <ButtonWithProps url='/register' label='sign up'></ButtonWithProps>;
      logInOrOutButton = <ButtonWithProps url='/login' label='log in' />;
    }
    return (
      <div>
        <div className={styles.space}></div>
        <header className={styles.header}>
          <Link to='/'><img className={styles.alien} src={logoFull} /></Link>
          <Dropdown defaultValue='Channels' dropdownList={dropdownListToBeRendered} buttonImage='http://t1.gstatic.com/images?q=tbn:ANd9GcThsotATP9ktYH_-oqNK6lYSI2USCxC-9nhbqScnKqvWFyxmL64' />
          {headerMessage}
          {logInOrOutButton}
          {registerOrSubmitButton}
        </header >
      </div>
    );
  }
}

Header.propTypes = {
  isLoggedIn: PropTypes.bool,
  logout: PropTypes.func,
  loadChannels: PropTypes.func,
  channels: PropTypes.array,
};

const fetchChannels = () => {
  return function (dispatch) {
    callApi('/channels', 'get')
      .then(resp => {
        dispatch({
          type: 'SUCCESSFULLY_LOADED_CHANNELS',
          payload: resp.data,
        });
      })
      .catch(error => {
        dispatch({
          type: 'UNSUCCESSFULLY_LOADED_CHANNELS',
          payload: error,
        });
      });
  };
};

const mapStateToProps = (state) => {
  return {
    isLoggedIn: state.isLoggedIn,
    channels: state.channels,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    logout: () => dispatch({ type: 'SET_LOGOUT_STATUS', payload: false }),
    loadChannels: () => dispatch(fetchChannels()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Header);
