import React from 'react';
import PropTypes from 'prop-types';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import { withStyles } from '@material-ui/core/styles';
import CreateIcon from '@material-ui/icons/Create';
import FormHelperText from '@material-ui/core/FormHelperText';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import SendIcon from '@material-ui/icons/Send';
import { withRouter } from "react-router-dom";
import callApi from '../API/callApi';

const useStyles = theme => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white,
    },
  },
  paper: {
    marginTop: theme.spacing(6),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  listAvatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.primary.main,
  },
  buttonContainer: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
  button: {
    margin: theme.spacing(2, 0, 0, 0),
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  counter: {
    textAlign: 'right',
  },
  formControl: {
    minWidth: 120,
  },
  selectButton: {
    textAlign: 'right',
  },
});

class SubmitPostPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: '',
      titleHelperText: '*Required',
      isTitleError: false,
      content: '',
      contentHelperText: '*Required',
      isContentError: false,
      counter: 255,
      selectedChannelId: 0,
      redirect: false,
      channels: [],
    };
  }

  componentDidMount() {
    callApi('/channels', 'get')
      .then(resp => {
        this.setState({ channels: resp.data });
      })
      .catch(error => console.log(error));
  }

  handleContentChange = (e) => {
    this.setState({
      content: e.target.value,
      counter: 255 - e.target.value.length,
    }, () => {
      if (this.state.counter < 0) {
        this.setState({
          contentHelperText: 'Content can\'t be longer than 255 characters',
          isContentError: true,
        });
      } else {
        this.setState({
          contentHelperText: '*Required',
          isContentError: false,
        });
      }
    });
  }

  handleContentOnBlur = () => {
    if (this.state.content === '') {
      this.setState({
        isContentError: true,
        contentHelperText: 'Content is required!',
      });
    }
  }

  handleSelectChange = (e) => {
    this.setState({
      selectedChannelId: e.target.value,
    });
  }

  handleTitleChange = (e) => {
    this.setState({
      title: e.target.value,
    });
    if (e.target.value) {
      this.setState({
        isTitleError: false,
        titleHelperText: '*Required',
      });
    }
  }

  handleTitleOnBlur = () => {
    if (this.state.title === '') {
      this.setState({
        isTitleError: true,
        titleHelperText: 'Title is required!',
      });
    }
  }

  handleSubmit = (e) => {
    e.preventDefault();
    const requestBody = {
      title: this.state.title,
      content: this.state.content,
      channelId: this.state.selectedChannelId,
    };
    callApi('/posts', 'post', requestBody)
      .then(() => {
        this.props.history.push('/');
      })
      .catch(error => console.log(error));
  }

  render() {
    const { classes } = this.props;
    const {
      channels,
      counter,
      title,
      titleHelperText,
      isTitleError,
      content,
      selectedChannelId,
      isContentError,
      contentHelperText,
    } = this.state;
    return (
      <>
        < Container component="main" maxWidth="sm" >
          <CssBaseline />
          <div className={classes.paper}>
            <Avatar className={classes.avatar}>
              <CreateIcon />
            </Avatar>
            <Typography component="h1" variant="h5">
              Submit a new post
        </Typography>
            <form onSubmit={this.handleSubmit} className={classes.form} noValidate>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <FormControl fullWidth variant="outlined" className={classes.formControl}>
                    <Select
                      value={selectedChannelId}
                      displayEmpty
                      onChange={this.handleSelectChange}
                      inputProps={{
                        name: 'age',
                        id: 'outlined-category-simple',
                      }}
                    >
                      <MenuItem value="">
                        <strong>-- Please select channel --</strong>
                      </MenuItem>
                      {channels.length === 0 ? null : channels.map(channel => {
                        return <MenuItem
                          key={channel.id}
                          value={channel.id}>
                          <Avatar
                            alt={channel.name}
                            src={channel.background_image}
                            className={classes.listAvatar} />
                          {channel.name}
                        </MenuItem>;
                      })}
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    error={isTitleError}
                    variant="outlined"
                    helperText={titleHelperText}
                    required
                    fullWidth
                    id="title"
                    label="Title"
                    name="title"
                    value={title}
                    autoComplete="uname"
                    onChange={this.handleTitleChange}
                    onBlur={this.handleTitleOnBlur}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    required
                    id="outlined-multiline-static"
                    label="Content"
                    multiline
                    rows="5"
                    error={isContentError}
                    helperText={contentHelperText}
                    className={classes.textField}
                    margin="normal"
                    variant="outlined"
                    value={content}
                    onChange={this.handleContentChange}
                    onBlur={this.handleContentOnBlur}
                  />
                  <FormHelperText
                    classes={{
                      root: classes.counter,
                    }}>
                    {counter} / 255
                </FormHelperText>
                </Grid>
              </Grid>
              <div className={classes.buttonContainer}>
                <Button
                  disabled={!(!!title && !!content && !!selectedChannelId && !isContentError)}
                  classes={{ root: classes.selectButton }}
                  variant="contained"
                  type="submit"
                  color="primary"
                  size="large"
                  className={classes.button}
                  endIcon={<SendIcon />}
                >
                  Submit
              </Button>
              </div>
            </form>
          </div>
        </Container >
      </>
    );
  }
}

SubmitPostPage.propTypes = {
  classes: PropTypes.object,
  history: PropTypes.object,
};

export default withRouter(withStyles(useStyles)(SubmitPostPage));
