import React, { Component } from 'react';
import styles from './style.scss';
import { connect } from 'react-redux';
import propTypes from 'prop-types';

class HelloWorld extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    const { target } = event;
    const { value } = target;
    this.setState({ value });
  }

  handleSubmit(event) {
    event.preventDefault();
    const { value } = this.state;
    this.props.onSubmit(value);
  }

  render() {
    return (
      <main>
        <h1 className={styles.title}>Hello React!</h1>
        <form onSubmit={this.handleSubmit}>
          <label className={styles.label} htmlFor="text">Text</label>
          <input className={styles.input} type="text" id="text" value={this.state.value} onChange={this.handleChange} />
          <input className={styles.button} type="submit" value="Reverse" />
        </form>
        <span>{this.props.reversed}</span>
      </main>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    reversed: state.reversed
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onSubmit: (payload) => dispatch({ type: 'REVERSE', payload }),
  };
};

HelloWorld.propTypes = {
  onSubmit: propTypes.func.isRequired,
  reversed: propTypes.string.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(HelloWorld);
