import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import callApi from '../../API/callApi';
import { useHistory } from "react-router-dom";
import InputAdornment from '@material-ui/core/InputAdornment';
import IconButton from '@material-ui/core/IconButton';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import ErrorIcon from '@material-ui/icons/Error';
import { connect } from 'react-redux';
import propTypes from 'prop-types';

const useStyles = makeStyles(theme => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white,
    },
  },
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

function Register(props) {
  const [usernameInput, setUsernameInput] = React.useState('');
  const [passwordInput, setPasswordInput] = React.useState('');
  const [isUsernameError, setIsUsernameError] = React.useState(false);
  const [isPasswordError, setIsPasswordError] = React.useState(false);
  const [usernameHelperText, setUsernameHelperText] = React.useState('*Required');
  const [passwordHelperText, setPasswordHelperText] = React.useState('*Required');
  const [showPassword, setShowPassword] = React.useState(false);

  const classes = useStyles();
  const history = useHistory();

  const usernameOnBlurHandler = () => {
    if (usernameInput === '') {
      setIsUsernameError(true);
      setUsernameHelperText('Error: Username is required');
    }
  };

  const passwordOnBlurHandler = () => {
    if (passwordInput === '') {
      setIsPasswordError(true);
      setPasswordHelperText('Error: Password is required');
    }
  };

  const handleUsernameChange = (e) => {
    setUsernameInput(e.target.value);
    if (e.target.value) {
      setIsUsernameError(false);
      setUsernameHelperText('*Required');
    }
  };

  const handlePasswordChange = (e) => {
    setPasswordInput(e.target.value);
    if (e.target.value) {
      setIsPasswordError(false);
      setPasswordHelperText('*Required');
    }
  };

  const submitHandler = (e) => {
    e.preventDefault();
    const requestBody = {
      username: usernameInput,
      password: passwordInput
    };
    return callApi('/register', 'post', requestBody)
      .then(
        (response) => {
          localStorage.setItem('token', response.data.token);
          localStorage.setItem('refreshToken', response.data.refreshToken);
          props.login();
        },
        (error) => {
          if (error.request.status === 400) {
            setIsUsernameError(true);
            setUsernameHelperText('Username is already taken');
          }
        });
  };

  const renderRedirect = () => {
    if (props.isLoggedIn) {
      return history.push('/');
    }
  };

  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleMouseDownPassword = (e) => {
    e.preventDefault();
  };

  return (
    <Container component="main" maxWidth="xs" >
      {renderRedirect()}
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}
          src='http://icons.iconarchive.com/icons/papirus-team/papirus-apps/512/emerald-theme-manager-icon-icon.png'>
        </Avatar>
        <Typography component="h1" variant="h5">
          Register
        </Typography>
        <form className={classes.form} noValidate>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                error={isUsernameError}
                required
                helperText={usernameHelperText}
                fullWidth
                id="username"
                label="Username"
                name="username"
                value={usernameInput}
                onChange={handleUsernameChange}
                autoComplete="uname"
                onBlur={(e) => usernameOnBlurHandler(e)}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton
                        edge="end"
                        aria-label="error-icon"
                        tabIndex='-1'
                        disabled={true}
                      >
                        {isUsernameError ? <ErrorIcon style={{ color: '#F44237' }} /> : null}
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                error={isPasswordError}
                required
                fullWidth
                helperText={passwordHelperText}
                name="password"
                label="Password"
                type={showPassword ? 'text' : 'password'}
                id="password"
                value={passwordInput}
                onChange={handlePasswordChange}
                autoComplete="current-password"
                onBlur={(e) => passwordOnBlurHandler(e)}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton
                        edge="end"
                        aria-label="toggle password visibility"
                        onClick={handleClickShowPassword}
                        onMouseDown={handleMouseDownPassword}
                        tabIndex='-1'
                      >
                        {isPasswordError ? <ErrorIcon style={{ color: '#F44237', marginRight: '10px' }} /> : null}
                        {showPassword ? <VisibilityOff /> : <Visibility />}
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
              />
            </Grid>
          </Grid>
          <Button
            disabled={!(!!usernameInput && !!passwordInput)}
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            onClick={submitHandler}
          >
            Register
          </Button>
          <Grid container justify="flex-end">
            <Grid item>
              <Link href="/login" variant="body2">
                Already have an account? Log in
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={5}>
        <Typography variant="body2" color="textSecondary" align="center">
          {'Copyright © '}
          <Link color="inherit" target="_blank" rel="noopener" href="https://github.com/green-fox-academy/">
            Griseus Emerald Reddit
          </Link>{' '}
          {new Date().getFullYear()}
          {'.'}
        </Typography>
      </Box>
    </Container >
  );
}

const mapStateToProps = (state) => {
  return {
    isLoggedIn: state.isLoggedIn
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    login: () => dispatch({ type: 'SET_LOGIN_STATUS', payload: true }),
  };
};

Register.propTypes = {
  login: propTypes.func.isRequired,
  isLoggedIn: propTypes.bool.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(Register);
