import React from 'react';
import PropTypes from 'prop-types';
import styles from './style.css';
import showRelativeTime from '../../utilities/relativeTime/relativeTime';
import EditRoundedIcon from '@material-ui/icons/EditRounded';
import DeleteRoundedIcon from '@material-ui/icons/DeleteRounded';
import { connect } from 'react-redux';
import getUsernameFromToken from '../../utilities/getUsernameFromToken';
import callApi from '../../API/callApi';
import Modal from '../Modal';
import { Button, TextField, Typography } from '@material-ui/core';
import Warning from '../../img/warning-bgr.png';
import Info from '../../img/info-bgr.png';

const handleDelete = (id) => (dispatch) => {
  callApi(`/posts/${id}`, 'DELETE')
    .then(resp => {
      console.log(resp.statusText);
      dispatch({ type: 'DELETE_POST', payload: id });
    })
    .catch(err => console.log(err));
};

const handleEdit = (id, editedTitle, editedContent) => (dispatch) => {
dispatch({ type: 'EDIT_POST', payload: {id, title: editedTitle, content: editedContent} });
callApi(`/posts/${id}`, 'PUT')
  .then(resp => {
    console.log(resp.statusText);
    // TODO: put dispatch here
  })
  .catch(err => console.log(err));
};

const Post = (props) => {
  const [isModalOpen, setIsModalOpen] = React.useState(false);
  const [isEditOpen, setIsEditOpen] = React.useState(false);
  const [editedTitle, setEditedTitle] = React.useState(props.item.title);
  const [editedContent, setEditedContent] = React.useState(props.item.content);
  const {
    id,
    title,
    channel_name: channelName,
    channel_image: channelImage,
    content,
    time_entered: timeEntered,
    author
  } = props.item;
  const loggedInUser = getUsernameFromToken();

  const handleTrashClick = () => {
    setIsModalOpen(true);
  };

  const handleEditClick = () => {
    setIsEditOpen(true);
  };

  const handleClose = () => {
    setIsModalOpen(false);
  };

  const handleEditClose = (id) => {
    props.editPost(id, editedTitle, editedContent);
    setIsEditOpen(false);
  };

  const handleTitleEditChange = (e) => {
    setEditedTitle(e.target.value);
  };

  const handleContentEditChange = (e) => {
    setEditedContent(e.target.value);
  };

  return (
    <>
      <div className={styles.container}>
        <div className={styles.sidebar}>
          <div className={styles.votes}>
            <i className="fas fa-arrow-up"></i>
            <p>0</p>
            <i className="fas fa-arrow-down"></i>
          </div>
          {loggedInUser === author ? (
            <div className={styles.edits}>
              <EditRoundedIcon fontSize="small" className={styles.edit} onClick={() => handleEditClick()} />
              <DeleteRoundedIcon fontSize="small" className={styles.trash} onClick={() => handleTrashClick()} />
            </div>
          ) : null}
        </div>
        <div className={styles.content}>
          <div className={styles['content-header']}>
            <div className={styles.metadata}>
              <img src={channelImage} />
              <p className={styles['channel-name']}>r/{channelName}</p>
              <p>|</p>
              <p>Posted</p>
              <p className={styles.timestamp}>{showRelativeTime(timeEntered)}</p>
              <p>by {author}</p>
            </div>
            <div className={styles.title}>
              <h1>{title}</h1>
            </div>
          </div>
          <div className={styles['text-content']}>
            <p>
              {content}
            </p>
          </div>
        </div>
      </div>
      <Modal show={isModalOpen} onClose={handleClose} sideImage={Warning}>
        <h5>Do you really want to hurt me?</h5>
        <br />
        <Button onClick={handleClose}>Oh, no!</Button>
        <Button style={{ marginLeft: 8 }} variant="contained" color="secondary" onClick={() => props.deletePost(id)}>Yes!</Button>
      </Modal>
      <Modal show={isEditOpen} onClose={handleEditClose} sideImage={Info}>
        <Typography variant="h5" >Edit post</Typography>
        <TextField
          fullWidth
          style={{ marginTop: 16 }}
          label="Title"
          variant="outlined"
          value={editedTitle}
          onChange={handleTitleEditChange}
        />
        <TextField
          fullWidth
          style={{ marginTop: 16 }}
          label="Content"
          variant="outlined"
          multiline rows="6"
          value={editedContent}
          onChange={handleContentEditChange}
        />
        <div style={{marginTop: 20}}>
          <Button onClick={handleEditClose}>Cancel</Button>
          <Button style={{ marginLeft: 8 }} variant="contained" color="primary" onClick={() => handleEditClose(id)}>Update</Button>
        </div>
      </Modal>
    </>
  );
};

Post.propTypes = {
  item: PropTypes.shape({
    id: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    content: PropTypes.string.isRequired,
    channel_name: PropTypes.string.isRequired,
    channel_image: PropTypes.string,
    time_entered: PropTypes.number.isRequired,
    author: PropTypes.string.isRequired,
  }),
  deletePost: PropTypes.func.isRequired,
  editPost: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => {
  return {
    isLoggedIn: state.isLoggedIn,
    isEditModalOpen: state.isEditModalOpen,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    deletePost: (id) => dispatch(handleDelete(id)),
    editPost: (id, editedTitle, editedContent) => dispatch(handleEdit(id, editedTitle, editedContent)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Post);
