import React, { Component } from 'react';
import BSModal from 'react-bootstrap/Modal';
import styles from './style.css';
import PropTypes from 'prop-types';

class Modal extends Component {
  constructor(props) {
    super(props);
    this.handleClose = this.handleClose.bind(this);
  }

  handleClose() {
    this.props.onClose();
  }

  render() {
    return (
      <BSModal centered show={this.props.show} onHide={this.handleClose} >
        <BSModal.Body className={styles.modal} >
          <div className={styles.sidebar} style={{ backgroundImage: `url(${this.props.sideImage})` }}></div>
          <div className={styles['modal-body']} >
            {this.props.children}
          </div>
          <span className={styles['close-icon']}>
            <i className="fas fa-times" onClick={this.handleClose}></i>
          </span>
        </BSModal.Body>
      </BSModal>
    );
  }
}

Modal.propTypes = {
  sideImage: PropTypes.string,
  children: PropTypes.node.isRequired,
  show: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
};

export default Modal;
