import React, { Component } from 'react';
import Modal from '../components/Modal';
import BSButton from 'react-bootstrap/Button';
import HelloWorld from '../components/HelloWorld';
import errorBGR from '../img/error-bgr.png';
import Dropdown from '../components/Dropdown';
import Header from '../components/Header';

const errorMessage = 'Hi, I\'m an ERROR message sent from props!';

const dropdownListToBeRendered = [{
  href: '1',
  text: 'JavaScript',
  image: 'https://a.thumbs.redditmedia.com/zDOFJTXd6fmlD58VDGypiV94Leflz11woxmgbGY6p_4.png',
},
{
  href: '2',
  text: 'Node',
  image: 'https://b.thumbs.redditmedia.com/8RJ1zsSxLbTrSrRAhziwMynfkWVcuFNMXPsLqtGct1o.png',
},
{
  href: '3',
  text: 'Spotify',
  image: 'https://dashboard.snapcraft.io/site_media/appmedia/2017/12/spotify-linux-256.png',
},
{
  href: '4',
  text: 'React',
  image: 'https://styles.redditmedia.com/t5_2zldd/styles/communityIcon_12zd9qt7we121.jpg',
},
{
  href: '5',
  text: 'JavaScript',
  image: 'https://a.thumbs.redditmedia.com/zDOFJTXd6fmlD58VDGypiV94Leflz11woxmgbGY6p_4.png',
}
];

class HelloWorldPage extends Component {
  state = {
    isModalShown: false,
  }

  render() {
    const showModal = () => {
      this.setState({
        isModalShown: true,
      });
    };

    const hideModal = () => {
      this.setState({
        isModalShown: false,
      });
    };

    return (
      <>
      <Header />
        <HelloWorld />
        <BSButton onClick={showModal}>Show Modal</BSButton>
        <Modal sideImage={errorBGR} show={this.state.isModalShown} onClose={hideModal}>
          {errorMessage}
        </Modal>
        <Dropdown defaultValue='Channels' dropdownList={dropdownListToBeRendered} buttonImage='http://t1.gstatic.com/images?q=tbn:ANd9GcThsotATP9ktYH_-oqNK6lYSI2USCxC-9nhbqScnKqvWFyxmL64' />
      </>
    );
  }
}

export default HelloWorldPage;
