'use strict';

const initialState = {
  reversed: '',
  posts: [],
  channels: [],
  isLoaded: false,
  error: null,
  isLoggedIn: false,
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case 'REVERSE':
      return {
        ...state,
        reversed: action.payload.split('').reverse().join('')
      };
    case 'SUCCESFULLY_LOADED_POSTS':
      return {
        ...state,
        posts: action.payload,
        isLoaded: true,
      };
    case 'UNSUCCESFULLY_LOADED_POSTS':
      return {
        ...state,
        error: action.payload,
        isLoaded: true
      };
    case 'SET_LOGIN_STATUS':
      return {
        ...state,
        isLoggedIn: action.payload
      };
    case 'SET_LOGOUT_STATUS':
      return {
        ...state,
        isLoggedIn: action.payload
      };
    case 'DELETE_POST':
      return {
        ...state,
        posts: state.posts.filter(e => e.id !== action.payload),
      };
    case 'SUCCESSFULLY_LOADED_CHANNELS':
      return {
        ...state,
        channels: action.payload,
      };
    case 'UNSUCCESSFULLY_LOADED_CHANNELS':
      return {
        ...state,
        error: action.payload,
      };
    case 'EDIT_POST':
      return {
        ...state,
        posts: state.posts.map(post => {
          return post.id === action.payload.id 
          ? {...post, title: action.payload.title, content: action.payload.content}
          : post;
        }),
      };
  }
  return state;
};

export default reducer;
