import React from 'react';
import ReactDOM from 'react-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import HelloWorldPage from './pages/HelloWorldPage';
import RegisterPage from './pages/RegisterPage';
import LoginPage from './pages/LoginPage';
import Home from './pages/HomePage';
import SubmitPost from './pages/SubmitPostPage';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import { createStore, applyMiddleware, compose } from 'redux';
import reducer from './store/reducer';
import { Provider } from 'react-redux';
import thunk from 'redux-thunk';

class App extends React.Component {
  render() {
    return (
      <Router>
        <Route exact path="/hello" component={HelloWorldPage} />
        <Route exact path="/register" component={RegisterPage} />
        <Route exact path="/login" component={LoginPage} />
        <Route exact path="/" component={Home} />
        <Route exact path="/submit" component={SubmitPost} />
      </Router>
    );
  }
}

const middleware = window.__REDUX_DEVTOOLS_EXTENSION__ ?
  compose(
    applyMiddleware(thunk),
    window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__(),
  ) : applyMiddleware(thunk);

const store = createStore(reducer, middleware);

if (localStorage.getItem('token')) {
  store.dispatch({ type: 'SET_LOGIN_STATUS', payload: true });
}

const mountNode = document.getElementById('root');
ReactDOM.render(<Provider store={store}><App /></Provider>, mountNode);
