import React, { Component } from 'react';
import styles from './styles.css';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

class ButtonWithProps extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <Link onClick={this.props.onClick} to={this.props.url}><button className={styles.logIn}> {this.props.label} </button></Link>
    );
  }
}
ButtonWithProps.propTypes = {
  url: PropTypes.string,
  label: PropTypes.string,
  onClick: PropTypes.func,
};

module.exports = ButtonWithProps;
